import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
  Box,
  Card,
  CardContent,
  LinearProgress,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Chip,
  Alert
} from '@mui/material';
import {
  School,
  CheckCircle,
  Help,
  TrendingUp,
  PlayArrow,
  Close,
  SkipNext,
  HourglassEmpty,
  ExpandMore,
  ExpandLess
} from '@mui/icons-material';
import { useDatabase } from '../contexts/DatabaseContext';
import { getAllWordLists, getWordsFromList, WordList } from '../types/wordLists';
import { useWordProgress } from '../hooks/usePersistentState';

// Kunskapsniver fr startguiden
export type KnowledgeLevel = 'nyborjare' | 'lite_erfaren' | 'erfaren' | 'proffs';

// Interface fr kunskapsniv-frga
interface KnowledgeLevelQuestion {
  id: string;
  level: KnowledgeLevel;
  title: string;
  description: string;
}

// Interface fr individuella ordlistor-frgor
interface WordListQuestion {
  id: string;
  wordList: WordList;
  selectedAnswer: 'ja' | 'behover_repetera' | 'nej' | 'vanta' | null;
}

// Interface fr start-guide frgor (baktkompatibilitet)
interface GuideQuestion {
  id: string;
  question: string;
  wordListId: string;
  wordListName: string;
  difficulty: string;
  words: any[];
  showWords: boolean;
}

// Props fr StartGuideDialog
interface StartGuideDialogProps {
  open: boolean;
  onClose: () => void;
  onComplete: () => void;
}

// Start-guide dialog fr nya anvndare
const StartGuideDialog: React.FC<StartGuideDialogProps> = ({ open, onClose, onComplete }) => {
  const { wordDatabase } = useDatabase();
  const { wordProgress, setWordLevel, setWordProgress } = useWordProgress();
  
  // Ny struktur fr startguiden
  const [currentStep, setCurrentStep] = useState<'intro' | 'knowledge_level' | 'wordlists' | 'completed'>('intro');
  // Ordning p steg fr navigering (exkluderar completed)
  const stepOrder: Array<'intro' | 'knowledge_level' | 'wordlists'> = ['intro', 'knowledge_level', 'wordlists'];
  const currentStepIndex = stepOrder.indexOf(currentStep as any);

  const goToStepIndex = (index: number) => {
    if (index >= 0 && index < stepOrder.length) {
      setCurrentStep(stepOrder[index]);
    }
  };

  const handleNext = () => {
    // Frenklad version - bara stng startguiden nr man trycker "Kom igng"
    if (currentStep === 'intro') {
      handleFinish();
      return;
    }
    if (currentStep === 'wordlists') return; // sista steget innan slutfr
    goToStepIndex(currentStepIndex + 1);
  };

  const handleBack = () => {
    if (currentStep === 'intro') return;
    goToStepIndex(currentStepIndex - 1);
  };
  const [selectedKnowledgeLevel, setSelectedKnowledgeLevel] = useState<KnowledgeLevel | null>(null);
  const [wordListQuestions, setWordListQuestions] = useState<WordListQuestion[]>([]);
  
  // Baktkompatibilitet - gamla state
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [questions, setQuestions] = useState<GuideQuestion[]>([]);
  const [answers, setAnswers] = useState<{ [questionId: string]: 'ja' | 'delvis' | 'nej' | 'hoppa' }>({});
  const [isCompleted, setIsCompleted] = useState(false);
  const [totalWordsAdded, setTotalWordsAdded] = useState(0);
  const [showDetailedList, setShowDetailedList] = useState(false);

  // Rkna antal ord i varje kategori
  const getWordCounts = () => {
    const counts = { attLaraMig: 0, larda: 0 };
    Object.values(wordProgress).forEach((progress: any) => {
      if (progress.level === 1) counts.attLaraMig++;
      else if (progress.level === 2) counts.larda++;
    });
    return counts;
  };

  const wordCounts = getWordCounts();

  // Gruppera ordlistor baserat p valt svar
  const getGroupedWordLists = () => {
    const groups = {
      ja: [] as WordListQuestion[],
      behover_repetera: [] as WordListQuestion[],
      nej: [] as WordListQuestion[],
      vanta: [] as WordListQuestion[]
    };

    wordListQuestions.forEach(question => {
      if (question.selectedAnswer && groups[question.selectedAnswer]) {
        groups[question.selectedAnswer].push(question);
      }
    });

    return groups;
  };

  // Kunskapsniv-frgor
  const knowledgeLevelQuestions: KnowledgeLevelQuestion[] = [
    {
      id: 'nyborjare',
      level: 'nyborjare',
      title: 'Nybrjare',
      description: 'Jag har aldrig tecknat frut'
    },
    {
      id: 'lite_erfaren',
      level: 'lite_erfaren',
      title: 'Lite erfaren',
      description: 'Jag kan ngra grundlggande tecken'
    },
    {
      id: 'erfaren',
      level: 'erfaren',
      title: 'Erfaren',
      description: 'Jag kan mnga tecken och ngra fraser'
    },
    {
      id: 'proffs',
      level: 'proffs',
      title: 'Proffs',
      description: 'Jag kan teckna'
    }
  ];

  // Logik fr automatisk placering baserat p kunskapsniv och svrighetsgrad
  const getDefaultPlacement = (knowledgeLevel: KnowledgeLevel, difficulty: string): 'ja' | 'behover_repetera' | 'nej' => {
    const difficultyLevels = ['nyborjare', 'lite_erfaren', 'erfaren', 'proffs'];
    const userLevelIndex = difficultyLevels.indexOf(knowledgeLevel);
    const wordListLevelIndex = difficultyLevels.indexOf(difficulty as any);
    
    if (wordListLevelIndex === -1) return 'nej'; // Oknd svrighetsgrad
    
    const levelDifference = userLevelIndex - wordListLevelIndex;
    
    if (levelDifference === 0) {
      // Samma svrighetsgrad - behver lra mig
      return 'nej';
    } else if (levelDifference === 1) {
      // En niv lgre - behver repetera
      return 'behover_repetera';
    } else if (levelDifference >= 2) {
      // Tv eller fler niver lgre - kan redan
      return 'ja';
    } else {
      // Hgre svrighetsgrad n anvndaren - behver lra mig
      return 'nej';
    }
  };

  // Generera individuella ordlistor-frgor nr kunskapsniv r vald
  useEffect(() => {
    if (selectedKnowledgeLevel && Object.keys(wordDatabase).length > 0) {
      const allLists = getAllWordLists(wordDatabase);
      
      // Filtrera ordlistor baserat p svrighetsniv (samma eller lgre n vald niv)
      const difficultyLevels = ['nyborjare', 'lite_erfaren', 'erfaren', 'proffs'];
      const userLevelIndex = difficultyLevels.indexOf(selectedKnowledgeLevel);
      
      const guideLists = allLists
        .filter(list => {
          const listLevelIndex = difficultyLevels.indexOf(list.difficulty);
          return listLevelIndex <= userLevelIndex; // Samma eller lgre svrighetsgrad
        })
        .sort((a, b) => {
          const levelA = difficultyLevels.indexOf(a.difficulty);
          const levelB = difficultyLevels.indexOf(b.difficulty);
          return levelB - levelA; // Hgre svrighetsgrad frst
        });
      
      const generatedWordListQuestions: WordListQuestion[] = guideLists.map((wordList, index) => ({
        id: `wordlist_${index}`,
        wordList: wordList,
        selectedAnswer: getDefaultPlacement(selectedKnowledgeLevel, wordList.difficulty)
      }));
      
      setWordListQuestions(generatedWordListQuestions);
    }
  }, [selectedKnowledgeLevel, wordDatabase]);

  // Generera frgor baserat p ordlistor (baktkompatibilitet)
  useEffect(() => {
    if (Object.keys(wordDatabase).length > 0) {
      const allLists = getAllWordLists(wordDatabase);
      
      // Vlj ut ordlistor som r markerade fr start-guiden
      const guideLists = allLists
        .filter(list => list.showInStartGuide === true)
        .sort((a, b) => (a.startGuidePosition || 999) - (b.startGuidePosition || 999));

      const generatedQuestions: GuideQuestion[] = guideLists
        .filter(list => {
          // Hoppa ver ordlistor dr alla ord redan r markerade som "att lra mig" eller "lrda"
          const wordsInList = getWordsFromList(list, wordDatabase);
          const hasUnmarkedWords = wordsInList.some(word => {
            const progress = wordProgress[word.id];
            return !progress || progress.level === 0;
          });
          return hasUnmarkedWords;
        })
        .map((list, index) => {
          // Hmta orden frn ordlistan
          const wordsInList = getWordsFromList(list, wordDatabase);
          
          return {
            id: `q${index + 1}`,
            question: `Vill du lra dig ${list.name.toLowerCase()}?`,
            wordListId: list.id,
            wordListName: list.name,
            difficulty: list.difficulty,
            words: wordsInList,
            showWords: list.showWordsInStartGuide || false
          };
        });

      setQuestions(generatedQuestions);
    }
  }, [wordDatabase]);

  // Hantera val av kunskapsniv
  const handleKnowledgeLevelSelect = (level: KnowledgeLevel) => {
    setSelectedKnowledgeLevel(level);
    setCurrentStep('wordlists');
  };

  // Hantera svar p ordlistor-frgor
  const handleWordListAnswer = (questionId: string, answer: 'ja' | 'behover_repetera' | 'nej' | 'vanta') => {
    setWordListQuestions(prev => 
      prev.map(question => 
        question.id === questionId 
          ? { ...question, selectedAnswer: answer }
          : question
      )
    );
  };

  // Hantera svar p frgor (baktkompatibilitet)
  const handleAnswer = (answer: 'ja' | 'delvis' | 'nej' | 'hoppa') => {
    const currentQuestion = questions[currentQuestionIndex];
    if (!currentQuestion) return;

    setAnswers(prev => ({
      ...prev,
      [currentQuestion.id]: answer
    }));

    // Lgg till ord baserat p svar (om inte hoppa ver)
    if (answer !== 'hoppa') {
      const wordList = getAllWordLists(wordDatabase).find(list => list.id === currentQuestion.wordListId);
      if (wordList) {
        const wordsInList = getWordsFromList(wordList, wordDatabase);
        
        wordsInList.forEach((word, index) => {
          let level = 0;
          let points = 0;

          switch (answer) {
            case 'ja':
              level = 1; // Att lra mig
              points = 0;
              break;
            case 'nej':
              level = 2; // Lrd
              points = 5;
              break;
          }

          // Stt lastPracticed till tom strng s att orden hamnar sist i prioritetslistan
          // (de har ju inte vats nnu, bara lagts till frn startguiden)
          const neverPracticedTime = '';

          setWordLevel(word.id, level);
          setWordProgress(prev => ({
            ...prev,
            [word.id]: {
              ...prev[word.id],
              level,
              points,
              stats: {
                correct: 0,
                incorrect: 0,
                lastPracticed: neverPracticedTime,
                difficulty: 50
              }
            }
          }));
        });

        setTotalWordsAdded(prev => prev + wordsInList.length);
      }
    }

    // Kontrollera om anvndaren har tillrckligt mnga ord i "att lra mig" (50-150)
    const currentWordCounts = getWordCounts();
    const attLaraMigCount = currentWordCounts.attLaraMig;
    
    // G till nsta frga eller avsluta
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(prev => prev + 1);
    } else if (attLaraMigCount >= 50) {
      // Avsluta om anvndaren har minst 50 ord i "att lra mig"
      setIsCompleted(true);
    } else {
      // Fortstt med fler frgor om anvndaren inte har tillrckligt mnga ord
      // Generera fler frgor frn ordlistor som inte redan har frgats om
      const allLists = getAllWordLists(wordDatabase);
      const guideLists = allLists
        .filter(list => list.showInStartGuide === true)
        .sort((a, b) => (a.startGuidePosition || 999) - (b.startGuidePosition || 999));
      
      const askedListIds = questions.map(q => q.wordListId);
      const remainingLists = guideLists.filter(list => !askedListIds.includes(list.id));
      
      if (remainingLists.length > 0) {
        // Lgg till fler frgor frn terstende ordlistor
        const additionalQuestions: GuideQuestion[] = remainingLists
          .filter(list => {
            const wordsInList = getWordsFromList(list, wordDatabase);
            const hasUnmarkedWords = wordsInList.some(word => {
              const progress = wordProgress[word.id];
              return !progress || progress.level === 0;
            });
            return hasUnmarkedWords;
          })
          .slice(0, 5) // Lgg till max 5 fler frgor t gngen
          .map((list, index) => {
            const wordsInList = getWordsFromList(list, wordDatabase);
            return {
              id: `q${questions.length + index + 1}`,
              question: `Vill du lra dig ${list.name.toLowerCase()}?`,
              wordListId: list.id,
              wordListName: list.name,
              difficulty: list.difficulty,
              words: wordsInList,
              showWords: list.showWordsInStartGuide || false
            };
          });
        
        if (additionalQuestions.length > 0) {
          setQuestions(prev => [...prev, ...additionalQuestions]);
          setCurrentQuestionIndex(prev => prev + 1);
        } else {
          setIsCompleted(true);
        }
      } else {
        setIsCompleted(true);
      }
    }
  };

  // Slutfr startguiden med nya strukturen
  const handleFinishNewGuide = () => {
    let totalAdded = 0;
    
    wordListQuestions.forEach(question => {
      if (question.selectedAnswer) {
        const wordsInList = getWordsFromList(question.wordList, wordDatabase);
        
        wordsInList.forEach(word => {
          let level = 0;
          let points = 0;

          switch (question.selectedAnswer) {
            case 'ja':
              level = 2; // Lrd
              points = 5;
              break;
            case 'behover_repetera':
              level = 1; // Att lra mig
              points = 3;
              break;
            case 'nej':
              level = 1; // Att lra mig
              points = 0;
              break;
            case 'vanta':
              level = 0; // Ingen progress - vntar
              points = 0;
              break;
          }

          // Stt lastPracticed till tom strng s att orden hamnar sist i prioritetslistan
          const neverPracticedTime = '';

          setWordLevel(word.id, level);
          setWordProgress(prev => ({
            ...prev,
            [word.id]: {
              ...prev[word.id],
              level,
              points,
              stats: {
                correct: 0,
                incorrect: 0,
                lastPracticed: neverPracticedTime,
                difficulty: 50
              }
            }
          }));
          
          totalAdded++;
        });
      }
    });
    
    setTotalWordsAdded(totalAdded);
    setCurrentStep('completed');
  };

  // terstll guide
  const handleReset = () => {
    setCurrentStep('knowledge_level');
    setSelectedKnowledgeLevel(null);
    setWordListQuestions([]);
    setCurrentQuestionIndex(0);
    setAnswers({});
    setIsCompleted(false);
    setTotalWordsAdded(0);
  };

  // Stng dialog
  const handleClose = () => {
    onClose();
    handleReset();
  };

  // Avsluta guide (frenklad version - ingen progress ndras)
  const handleFinish = () => {
    onComplete();
    handleReset();
    // Ingen reload eller storage event - bara stng startguiden
  };

  // Baktkompatibilitet - gamla variabler
  const currentQuestion = questions[currentQuestionIndex];
  const progress = questions.length > 0 ? ((currentQuestionIndex + 1) / questions.length) * 100 : 0;

  return (
    <Dialog 
      open={open} 
      onClose={handleClose}
      maxWidth="sm"
      fullWidth
      PaperProps={{
        sx: { borderRadius: 2 }
      }}
    >
      <DialogTitle>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <School color="primary" />
            <Typography variant="h6">
              {currentStep === 'intro' && 'Vlkommen till TSP Skolan'}
              {currentStep === 'knowledge_level' && 'Vlkommen till TSP Skolan'}
              {currentStep === 'wordlists' && 'Lmpliga ordlistor'}
              {currentStep === 'completed' && 'Klart att brja va!'}
            </Typography>
          </Box>
          <Button
            onClick={handleClose}
            sx={{ minWidth: 'auto', p: 1 }}
          >
            <Close />
          </Button>
        </Box>
      </DialogTitle>

      <DialogContent>
      {currentStep === 'intro' && (
    <>
      {/* Vlkomsttext */}
      <Box sx={{ mb: 3, maxWidth: '700px', mx: 'auto' }}>
        <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
          Den hr sidan r utvecklad fr att frhoppningsvis gra det lite enklare och lite roligare att lra dig 
          <b> svenskt teckensprk</b>  oavsett om du r helt nybrjare eller redan kan lite grann.
        </Typography>

        <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
          Att vlja vilka ord man ska brja med r en hel vetenskap. 
          Jag landade i att fokusera p ord som bygger fullstndiga <b>exempelmeningar </b>
          s att du kommer igng med meningar som <b>frhoppningsvis gr att anvnda i vardagen</b>.
        </Typography>

        <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
          I appen brjar du med att trna p <b>ord</b> genom vningarna: 
          <br /> <b>Se tecknet</b>  du ser hur ett ord tecknas 
          <br /> <b>Teckna sjlv</b>  du fr testa att teckna sjlv
        </Typography>

        <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
          Nr du har lrt dig tillrckligt mnga ord som bygger upp en fullstndig <b>exempelmening </b> 
          blir meningen tillgnglig fr trning. P s stt gr du steg fr steg 
          frn enskilda ord till hela meningar.
        </Typography>

        <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
          Det finns ocks en separat vning fr att trna <b>bokstavering</b>. Mnga ord 
          i teckensprk bokstaveras och det kan g fort!
        </Typography>

        <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
          Ett stort tack riktas till <b>Teckensprkslexikon vid Stockholms Universitet</b>, 
          som gjort det mjligt att bygga denna sida!
        </Typography>
      </Box>

      {/* Call to action */}
    </>
  )}

        {currentStep === 'knowledge_level' && (
          <>
            {/* Kunskapsniv-frga */}

            <Typography variant="h6" gutterBottom sx={{ mb: 3 }}>
              Vilken beskrivning passar dig bst?
            </Typography>

            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              {knowledgeLevelQuestions.map((question) => (
                <Card 
                  key={question.id}
                  sx={{ 
                    cursor: 'pointer',
                    transition: 'all 0.2s'
                  }}
                  onClick={() => handleKnowledgeLevelSelect(question.level)}
                >
                  <CardContent>
                    <Typography variant="h6" gutterBottom>
                      {question.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {question.description}
                    </Typography>
                  </CardContent>
                </Card>
              ))}
            </Box>
          </>
        )}

        {currentStep === 'wordlists' && (
          <>
            {/* Sammanfattning hgst upp */}
            <Alert severity="success" sx={{ mb: 3 }}>
              {(() => {
                const groups = getGroupedWordLists();
                return (
                  <Box>
                    {groups.ja.length > 0 && (
                      <Box sx={{ mb: 2 }}>
                        <Typography variant="body2" sx={{ mb: 1, fontWeight: 'bold' }}>
                           Ordlistor du kan:
                        </Typography>
                        {groups.ja.map(q => (
                          <Typography key={q.id} variant="body2" sx={{ ml: 2, mb: 0.5 }}>
                            {q.wordList.name}
                          </Typography>
                        ))}
                      </Box>
                    )}
                    {groups.behover_repetera.length > 0 && (
                      <Box sx={{ mb: 2 }}>
                        <Typography variant="body2" sx={{ mb: 1, fontWeight: 'bold' }}>
                           Ordlistor att repetera:
                        </Typography>
                        {groups.behover_repetera.map(q => (
                          <Typography key={q.id} variant="body2" sx={{ ml: 2, mb: 0.5 }}>
                            {q.wordList.name}
                          </Typography>
                        ))}
                      </Box>
                    )}
                    {groups.nej.length > 0 && (
                      <Box sx={{ mb: 2 }}>
                        <Typography variant="body2" sx={{ mb: 1, fontWeight: 'bold' }}>
                           Ordlistor att lra dig:
                        </Typography>
                        {groups.nej.map(q => (
                          <Typography key={q.id} variant="body2" sx={{ ml: 2, mb: 0.5 }}>
                            {q.wordList.name}
                          </Typography>
                        ))}
                      </Box>
                    )}
                    {groups.vanta.length > 0 && (
                      <Box sx={{ mb: 2 }}>
                        <Typography variant="body2" sx={{ mb: 1, fontWeight: 'bold' }}>
                           Ordlistor att vnta med:
                        </Typography>
                        {groups.vanta.map(q => (
                          <Typography key={q.id} variant="body2" sx={{ ml: 2, mb: 0.5 }}>
                            {q.wordList.name}
                          </Typography>
                        ))}
                      </Box>
                    )}
                  </Box>
                );
              })()}
            </Alert>

            {/* Dropdown fr detaljerad hantering */}
            <Box sx={{ mb: 3 }}>
              <Button
                variant="outlined"
                onClick={() => setShowDetailedList(!showDetailedList)}
                endIcon={showDetailedList ? <ExpandLess /> : <ExpandMore />}
                sx={{ mb: 2, textTransform: 'none' }}
              >
                {showDetailedList ? 'Dlj detaljerad lista' : 'Visa detaljerad lista om du vill ndra'}
              </Button>

              {showDetailedList && (
                <>
                  <Alert severity="info" sx={{ mb: 3 }}>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                      <strong>Ja (kan redan)</strong> = Ordlistan lggs till i "Lrda"
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                      <strong>Behver repetera (vill va mer)</strong> = Ordlistan lggs till i "Att lra mig"
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                      <strong>Nej (vill lra mig)</strong> = Ordlistan lggs till i "Att lra mig"
                    </Typography>
                    <Typography variant="body2">
                      <strong>Vnta (inte nnu)</strong> = Ordlistan lggs inte till nnu
                    </Typography>
                  </Alert>


                  <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                    {wordListQuestions.map((question) => (
                <Card 
                  key={question.id} 
                  sx={{ 
                    mb: 2,
                    border: question.selectedAnswer ? '2px solid' : '1px solid',
                    borderColor: question.selectedAnswer ? 'primary.main' : 'divider',
                    transition: 'all 0.2s ease-in-out'
                  }}
                >
                  <CardContent sx={{ p: 3 }}>
                    <Typography variant="h6" gutterBottom sx={{ mb: 2 }}>
                      Kan du {question.wordList.name.toLowerCase()}?
                    </Typography>
                    
                    {/* Visa frsta orden i ordlistan */}
                    {(() => {
                      const wordsInList = getWordsFromList(question.wordList, wordDatabase);
                      
                      // Sortera orden - frsk frst numeriskt, sedan alfabetiskt
                      const sortedWords = wordsInList.sort((a, b) => {
                        const aNum = parseFloat(a.ord);
                        const bNum = parseFloat(b.ord);
                        
                        // Om bda r nummer, sortera numeriskt
                        if (!isNaN(aNum) && !isNaN(bNum)) {
                          return aNum - bNum;
                        }
                        
                        // Annars sortera alfabetiskt
                        return a.ord.localeCompare(b.ord, 'sv');
                      });
                      
                      // Visa s mnga ord som mjligt utan att gra rutan strre
                      let wordsToShow = [];
                      let currentLength = 0;
                      const maxLength = 80; // Max lngd fr att hlla rutan kompakt
                      
                      for (let i = 0; i < sortedWords.length; i++) {
                        const word = sortedWords[i].ord;
                        const testLength = currentLength + (i > 0 ? 2 : 0) + word.length; // +2 fr ', '
                        
                        if (testLength <= maxLength) {
                          wordsToShow.push(word);
                          currentLength = testLength;
                        } else {
                          break;
                        }
                      }
                      
                      const hasMoreWords = wordsToShow.length < sortedWords.length;
                      const displayText = wordsToShow.join(', ') + (hasMoreWords ? '...' : '');
                      
                      return (
                        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                          {displayText}
                        </Typography>
                      );
                    })()}

                    {/* Svarsalternativ med modern design */}
                    <Box sx={{ 
                      display: 'flex', 
                      flexDirection: { xs: 'column', sm: 'row' }, 
                      gap: 1.5,
                      flexWrap: 'wrap'
                    }}>
                      <Button
                        variant={question.selectedAnswer === 'ja' ? 'contained' : 'outlined'}
                        size="medium"
                        onClick={() => handleWordListAnswer(question.id, 'ja')}
                        disableRipple
                        disableTouchRipple
                        disableElevation
                        sx={{ 
                          flex: { xs: 1, sm: '0 1 auto' },
                          minWidth: { xs: '100%', sm: '120px' },
                          py: 1.5,
                          borderRadius: 2,
                          fontWeight: question.selectedAnswer === 'ja' ? 'bold' : 'normal',
                          backgroundColor: question.selectedAnswer === 'ja' ? 'success.main' : 'transparent',
                          color: question.selectedAnswer === 'ja' ? 'white' : 'success.main',
                          borderColor: 'success.main',
                          '&:hover': {
                            backgroundColor: question.selectedAnswer === 'ja' ? 'success.main' : 'transparent',
                            color: question.selectedAnswer === 'ja' ? 'white' : 'success.main',
                            borderColor: 'success.main'
                          }
                        }}
                      >
                        <Box sx={{ textAlign: 'center' }}>
                          <Box sx={{ fontWeight: 'bold' }}>Ja</Box>
                          <Box sx={{ fontSize: '0.75rem', opacity: 0.8 }}>(kan redan)</Box>
                        </Box>
                      </Button>
                      <Button
                        variant={question.selectedAnswer === 'behover_repetera' ? 'contained' : 'outlined'}
                        size="medium"
                        onClick={() => handleWordListAnswer(question.id, 'behover_repetera')}
                        disableRipple
                        disableTouchRipple
                        disableElevation
                        sx={{ 
                          flex: { xs: 1, sm: '0 1 auto' },
                          minWidth: { xs: '100%', sm: '120px' },
                          py: 1.5,
                          borderRadius: 2,
                          fontWeight: question.selectedAnswer === 'behover_repetera' ? 'bold' : 'normal',
                          backgroundColor: question.selectedAnswer === 'behover_repetera' ? 'warning.main' : 'transparent',
                          color: question.selectedAnswer === 'behover_repetera' ? 'white' : 'warning.main',
                          borderColor: 'warning.main',
                          '&:hover': {
                            backgroundColor: question.selectedAnswer === 'behover_repetera' ? 'warning.main' : 'transparent',
                            color: question.selectedAnswer === 'behover_repetera' ? 'white' : 'warning.main',
                            borderColor: 'warning.main'
                          }
                        }}
                      >
                        <Box sx={{ textAlign: 'center' }}>
                          <Box sx={{ fontWeight: 'bold' }}>Behver repetera</Box>
                          <Box sx={{ fontSize: '0.75rem', opacity: 0.8 }}>(vill va mer)</Box>
                        </Box>
                      </Button>
                      <Button
                        variant={question.selectedAnswer === 'nej' ? 'contained' : 'outlined'}
                        size="medium"
                        onClick={() => handleWordListAnswer(question.id, 'nej')}
                        disableRipple
                        disableTouchRipple
                        disableElevation
                        sx={{ 
                          flex: { xs: 1, sm: '0 1 auto' },
                          minWidth: { xs: '100%', sm: '120px' },
                          py: 1.5,
                          borderRadius: 2,
                          fontWeight: question.selectedAnswer === 'nej' ? 'bold' : 'normal',
                          backgroundColor: question.selectedAnswer === 'nej' ? '#2196F3' : 'transparent',
                          color: question.selectedAnswer === 'nej' ? 'white' : '#2196F3',
                          borderColor: '#2196F3',
                          '&:hover': {
                            backgroundColor: question.selectedAnswer === 'nej' ? '#2196F3' : 'transparent',
                            color: question.selectedAnswer === 'nej' ? 'white' : '#2196F3',
                            borderColor: '#2196F3'
                          }
                        }}
                      >
                        <Box sx={{ textAlign: 'center' }}>
                          <Box sx={{ fontWeight: 'bold' }}>Nej</Box>
                          <Box sx={{ fontSize: '0.75rem', opacity: 0.8 }}>(vill lra mig)</Box>
                        </Box>
                      </Button>
                      <Button
                        variant={question.selectedAnswer === 'vanta' ? 'contained' : 'outlined'}
                        size="medium"
                        onClick={() => handleWordListAnswer(question.id, 'vanta')}
                        disableRipple
                        disableTouchRipple
                        disableElevation
                        startIcon={<HourglassEmpty />}
                        sx={{ 
                          flex: { xs: 1, sm: '0 1 auto' },
                          minWidth: { xs: '100%', sm: '120px' },
                          py: 1.5,
                          borderRadius: 2,
                          fontWeight: question.selectedAnswer === 'vanta' ? 'bold' : 'normal',
                          backgroundColor: question.selectedAnswer === 'vanta' ? 'grey.500' : 'transparent',
                          color: question.selectedAnswer === 'vanta' ? 'white' : 'grey.600',
                          borderColor: 'grey.500',
                          '&:hover': {
                            backgroundColor: question.selectedAnswer === 'vanta' ? 'grey.500' : 'transparent',
                            color: question.selectedAnswer === 'vanta' ? 'white' : 'grey.600',
                            borderColor: 'grey.500'
                          }
                        }}
                      >
                        <Box sx={{ textAlign: 'center' }}>
                          <Box sx={{ fontWeight: 'bold' }}>Vnta</Box>
                          <Box sx={{ fontSize: '0.75rem', opacity: 0.8 }}>(inte nnu)</Box>
                        </Box>
                      </Button>
                    </Box>
                  </CardContent>
                </Card>
                    ))}
                  </Box>
                </>
              )}
            </Box>

            {/* Slutfr-knapp med modern design */}
            <Box sx={{ mt: 4, textAlign: 'center' }}>
              <Button
                variant="contained"
                size="large"
                onClick={handleFinishNewGuide}
                startIcon={<CheckCircle />}
                sx={{ 
                  px: 6, 
                  py: 2,
                  borderRadius: 3,
                  fontSize: '1.1rem',
                  fontWeight: 'bold',
                  background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
                  boxShadow: '0 3px 5px 2px rgba(33, 203, 243, .3)',
                  transition: 'all 0.3s ease-in-out'
                }}
              >
                Slutfr startguiden
              </Button>
            </Box>
          </>
        )}

        {currentStep === 'completed' && (
          /* Slutskrm */
          <Box sx={{ textAlign: 'center' }}>
            <CheckCircle sx={{ fontSize: 80, color: 'success.main', mb: 2 }} />
            <Typography variant="h5" gutterBottom>
              Perfekt! Start-guiden r klar
            </Typography>
            <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
              Jag har lagt till <strong>{totalWordsAdded} ord</strong>.
            </Typography>
          </Box>
        )}
      </DialogContent>

      <DialogActions sx={{ justifyContent: 'flex-end' }}>
        <Box>
          {currentStep !== 'intro' && currentStep !== 'completed' && (
            <Button onClick={handleBack} sx={{ mr: 1 }}>
              Tillbaka
            </Button>
          )}
          {currentStep === 'intro' && (
            <Button onClick={handleNext} variant="contained">
              Kom igng
            </Button>
          )}
          {currentStep === 'knowledge_level' && (
            <Button onClick={handleNext} variant="contained" disabled={!selectedKnowledgeLevel}>
              Nsta
            </Button>
          )}
          {currentStep === 'wordlists' && (
            <Button onClick={handleFinishNewGuide} variant="contained" startIcon={<CheckCircle />}>
              Slutfr
            </Button>
          )}
          {currentStep === 'completed' && (
            <Button onClick={handleFinish} variant="contained" startIcon={<PlayArrow />}>
              Brja va!
            </Button>
          )}
        </Box>
      </DialogActions>
    </Dialog>
  );
};

export default StartGuideDialog;
